/* 0. Reset ================================= */
@import 'reset';


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: #fff;	
	width: 100%;
	height: 100%;
	font-family: arial;
}

/* 2. Content ================================= */

.sect-1{
	background: url(../img/bg.png) top no-repeat;
	background-size: cover;
	height: 975px;
	width: 100%;
	border-bottom: 20px solid #fff;
}

header{
	.logo{
		width: 13%;
		height: 13%;
		margin: 68px 4.791666666666667%; //69/1440
		float: left;
	}

	a{
		float: right;
		margin: 68px 5% 68px 0;
		color: #00b4cb;

		&:link {
			text-decoration: none;
		}

		&:visited {
			text-decoration: none;
		}

		&:hover {
			text-decoration: none;
			color: #f69f8d;
			cursor: pointer;
		}

		&:active {
			text-decoration: none;
			color: #00b4cb;
		}
	}

	.link{
		position: absolute;
		right: 3%;
		top: 48px;
		margin-top: 20px;
		width: 1.5%;
		height: 3%;
	}
}


