.sect-2{
	background: #fffbd6;
	height: 530px;
	width: 100%;
	border-bottom: 20px solid #fff;
	display: flex;

	img{
		position: relative;
		top: -60px;
		margin-left: 10%;//192/1440
		
	}

	div{
		margin: 70px 10% 70px 2%;

		h2{
			color: #04b3c9;
			font-size: 30px;
			text-align: center;
			margin-bottom: 25px;
		}

		p{
			color: #014458;
			font-size: 17px;
			line-height: 20px;
			margin-bottom: 20px;
		}

		button{
			background: #04b3c9;
			border-radius: 15px;
			height: 57px;
			width: 65%;//252/1440
			color: #fff;
			font-size: 30px;
			margin-left: 20%;

			&:hover{
				background: #f69f8d;
				cursor: pointer;
			}
		}
	}
}

.sect-3{
	position: relative;
	background: url(../img/hills.png) top no-repeat;
	background-size: cover;
	height: 1500px;
	width: 100%;
	margin-top: 20px;

	h2{
		font-size: 30px;
		margin-left: 45%;
		margin-right: 15%;
		text-align: center;
		color: #014458;

		span{
			color: #04b3c9;
		}
	}
}

h3{
	font-size: 25px;
	color: #04b3c9;
	line-height: 33px;
}

.box{
	position: absolute;
	background: #fff;
	width: 28.125%;//405/1440
	display: flex;
	flex-direction: column;
	// justify-content: space-around;
	padding: 2% 5%;
	border: 3px solid #bdb8b7;
	line-height: 20px;

	button{
		height: 49px;
		width: 100%;
		border-radius: 13px;
		background: #04b3c9;
		color: #fff;
		font-size: 22px;
		margin-bottom: 5px;

		&:hover{
			background: #f69f8d;
			cursor: pointer;
		}
	}

	p{
		color: #004459;
		margin: 20px 0;
	}
}


.box-1{
	top: 20px;
	height: 470px;
	margin: 0 16%;

	ul{
		padding-left: 10%;

		li{
			color: #004459;
			list-style-type: disc;
			font-size: 16px;
			line-height: 25px;
		}
	}
}

.box-2{
	top: 380px;
	height: 300px;
	margin: 0 60%;

	form{
		li{
			color: #004459;
			list-style-type: none;
			font-size: 16px;
			line-height: 55px;

			input{
				border: 2px solid #004459;
				font-size: 16px;
				border-radius: 5px;
				margin-left: 3%;
			}
			
			select{
				border: 2px solid #004459;
				font-size: 16px;
			}
		}
	}
}

.box-3{
	top: 695px;
	height: 255px;
	margin: 0 16%;	
}

footer{
	background: #006e7d;
	display: flex;
	flex-wrap: wrap;
	color: #cae7ea;
	padding: 30px 10%;

	h4{
		font-size: 20px;
		padding-bottom: 30px
	}

	a{
		color: #cae7ea;
		line-height: 20px;

		&:link {
			text-decoration: none;
		}

		&:visited {
			text-decoration: none;
		}

		&:hover {
			text-decoration: none;
			color: #f69f8d;
			cursor: pointer;
		}

		&:active {
			text-decoration: none;
			color: #cae7ea;
		}
	}

	section{
		display: flex;
		flex-direction: row;
		width: 50%;
		
		.links{
			display: flex;
			flex-direction: column;
			margin-right: 5%;
		}
	}

	.copyright{
		margin-top: 20px;
		font-size: 13px;

	}
}


